@font-face {
  font-family: "Cormorant Garamond";
  src: local("Cormorant Garamond"), url("../fonts/CormorantGaramond-MediumItalic.woff2") format("woff2"), url("../fonts/CormorantGaramond-MediumItalic.woff") format("woff");
  font-weight: 500;
  font-style: italic;
  font-display: swap; }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  position: relative;
  min-width: 320px; }

a {
  text-decoration: none;
  color: #1d1613; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  pointer-events: none;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  text-indent: -9999px;
  white-space: nowrap;
  overflow: hidden; }

.container {
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
