  .header__form button {
    width: 16px;
    height: 16px;
    position: absolute;
    left: 12px;
    bottom: 12px; }
  .header__form svg {
    fill: #ffffff;
    width: 16px;
    height: 16px; }
  .header__menu {
    padding-bottom: 55px;
    flex-wrap: wrap;
    align-items: center; }
  .header__nav {
    padding-top: 0;
    padding-left: 45px;
    padding-right: 45px;
    border: none;
    position: absolute;
    top: 155px;
    width: 100%; }
    .header__nav--close {
      display: none; }
    .header__nav--nojs {
